<template>
  <div id="announce">
    <new-announce-slider></new-announce-slider>
    <div class="announce_list">

      <div class="search bar">
        <div class="form">
          <input placeholder="请输入搜索" name="cname" type="text" v-model="label">
          <span class="seahchspan" @click="search()">搜索</span>
        </div>
      </div>

      <br/>
      <br/>
      <br/>

      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <van-list
          v-model="loading"
          :finished="finished"
          @load="onLoad"
        >

          <ul>
            <li v-for="(item, index) in announceList" :key="index">
              <div class="left">

              <img v-lazy="getImgURL1(item.thumbnailUrl.thumbnailPelativePath)"
                     @click="$router.push({
                                            path: detailURL,
                                            query:{id:item.id}
                                         })">
              </div>
              <div class="right">
                <h3 class="tit text-overflow"
                    @click="$router.push({
                                          path: detailURL,
                                          query:{id:item.id}
                                        })">
                  {{ item.title }}
                </h3>
                <div class="con con-overflow" v-html="item.content">{{ item.content }}</div>
                <p class="date">{{ timeStamp(item.publishTime) }}</p>
              </div>
            </li>
          </ul>
        </van-list>
        <div class="no_more" v-show="finished">没有更多了......</div>
      </van-pull-refresh>
    </div>

  </div>
</template>

<script>

  import Dev from '../../../config/dev.env'
  export default {
    name:"announce",
    data(){
      return{
        detailURL: '/announceDetail',
        announceList: [],
        loading: false,
        finished: false,
        isLoading: false,
        pageNum: 1, // 页数
        offset: 0,  // 下次加载起始页
        totalPage: 0, // 总页数
        label: '' // 输入框
      }
    },

    methods:{

      getImgURL1(imgURL) {
        let IMG_ROOT = Dev.API_ROOT;
        IMG_ROOT = IMG_ROOT.replace('"', '');
        if(imgURL){
          return  IMG_ROOT + '/file/' + imgURL;
        } else {
          return  IMG_ROOT + '/static/imgs/error.png';
        }
      },
      async search(){
        let that = this;
        that.pageNum=1
        let params = {
          offset: (that.pageNum - 1) * 8,
          label: that.label
        };
        let res = await this.$http.get(this.$HOST + '/open/health/message/center/message/search/find', params);
        if( res.result.total == 0){
          that.announceList = [];
        } else {
          let res1 = res.result.data;
          if(res.result){
            for(var i=0;i<res1.length;i++){
              if(res1[i].thumbnailUrl == null){
                res1[i].thumbnailUrl = '/static/imgs/error.png'
              }
            }
            that.announceList = res1;
          }
          that.pageNum = res.result.page; // 当前显示页
          that.totalPage = Math.ceil(res.result.total / res.result.limit); // 总页数
          if(res.result.total<8) {
            that.finished = true; // 加载结束
            that.isLoading = false;
            that.loading = false;
            return false;
          }
        }
       },
      async  init(){
        let that = this;
        let params = {
          offset: (that.pageNum - 1) * 8
        };
        let res = await this.$http.get(this.$HOST + '/open/health/message/center/app/messageList', params);
        let res1 = res.result.messages.data;
        if(res.result){
          for(var i=0;i<res1.length;i++){
            if(res1[i].thumbnailUrl == null){
              res1[i].thumbnailUrl = '/static/imgs/error.png'
            }
          }
          that.announceList = res1;
        }
        that.pageNum = res.result.messages.page; // 当前显示页
        that.totalPage = Math.ceil(res.result.messages.total / res.result.messages.limit); // 总页数
        if(res.result.messages.total<8) {
          that.finished = true; // 加载结束
          that.isLoading = false;
          that.loading = false;
          return false;
        }
      },

      async concatData(){
        let that = this;
        // that.pageNum += 1;
        that.pageNum += 1;

        let params = {
          offset: (that.pageNum - 1) * 8
        };

        let res = await this.$http.get(this.$HOST + '/open/health/message/center/app/messageList', params);
        if(res.result.messages.total<8) {
          that.finished = true; // 加载结束
          that.isLoading = false;
          that.loading = false;
          return false;
        }
        that.announceList = that.announceList.concat(res.result.messages.data);
        that.loading = false;

        if(that.pageNum >= that.totalPage){
          that.finished = true; // 加载结束
        }

      },

      onRefresh() {
        let that = this;
         setTimeout(() => {
          this.$toast({
            message: '刷新成功',
            position: 'bottom'
          });
          that.isLoading = false;
          that.pageNum = 1;
          that.loading = false;
          that.finished = false;
          that.isLoading = false;
          that.init();
        }, 500);

      },

      onLoad() {
        // 异步更新数据
        setTimeout(() => {
          this.concatData();
        }, 500);
      }
    },

    created(){

    },
    async mounted() {
      this.init();
    }
  }
</script>

<style scoped lang="scss">
  .no_more{
    font-size: 30px;
    color: #8d8d8d;
    text-align: center;
    margin: 40px auto 0;
  }

  .announce_list{

    position: relative;
    top: 460px;
    margin-bottom: 50px;

    ul{
      width: 93%;
      margin: auto;
      background: #FFF;
      padding-top: 30px;

      li{
        height: 150px;
        display: flex;
        padding-bottom: 25px;
        padding-left: 20px;

        &:not(:last-child){
          border-bottom: 5px solid #F5F5F5;
          margin-bottom: 25px;
        }

        .left{
          flex: .35;
          padding-right: 25px;
        }

        .right{
          flex: 1;
          min-width: 0; //解决文字溢出white-space和flex不兼容问题
          position: relative;

          h3{
            color: #000;
            font-size: 30px;
            padding-bottom: 5px;
          }

          .con{
            color: #8D8D8D;
            font-size: 28px;
          }

          .date{
            font-size: 28px;
            position: absolute;
            left: 0;
            bottom: 0;
          }
        }
      }
    }
  }
  div.search {
    padding:10px 0;
    position: fixed;
    top: 6rem;
    z-index: 9999;
    width: 100%;
    background: #ffffff;
    left: -4px;
  }

  .form {
    position:relative;
    width:700px;
    margin:2px auto;
  }

  .search input,button {

    border:none;

    outline:none;

  }

  .search input {
    width:100%;
    height:70px;
    padding-left:13px;
    font-size: 24px;
  }

  .search input {
    border:2px solid #c5464a;
    border-radius:5px;
    background:transparent;
    top:0;
    right:0;
  }

  .seahchspan {
    font-size: 24px;
    height:75px;
    cursor:pointer;
    position:absolute;
    line-height: 70px;
    background:#c5464a;
    border-radius:0 5px 5px 0;
    width:120px;
    top:0;
    right:-19px;
    color: #ffffff;
    text-align: center;
  }
</style>



